export default {
	data() {
		return {
			staff: []
		}
	},
	onLoad() {
		if(this.staff.length < 1) {
			uni.showLoading({
				title: '加载中...',
				mask: true
			})
		} 
		this.__init()
	},
	methods: {
		async __init() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "user_list"
			}).then((res) => {
				this.staff = res
				uni.hideLoading()
				if(this.staff.length < 1) {
					uni.showToast({
						title: "暂无数据",
						icon: "none",
						duration: 2500
					})
				}
			}).catch(() => {
				console.log("catch error!!");
			})
		}
	}
}
